<template>
    <div class="reducedPackaging flex-page">
        <navigation-bar  @onLeftClick="goBack" :pageName="typeVal==0?'满减优惠专区':'打包促销专区'" >
            <slot slot="nav-right" class="rightBar" name="right">
                <div class="rightBar" @click="goToShopCart">
                    <img ref="cartIcon" class="shopIcon" src="@/assets/images/newSlicing/icon_shop_cut.png" alt="">
                    <div class="goodsNum" :class="skuTypeNum>=100?'goodsNumMax':''" v-show="skuTypeNum!=0">
                        {{skuTypeNum<=99?skuTypeNum:'99+'}}
                    </div>
                </div>
            </slot>
        </navigation-bar>
        <div class="activityTitle">{{activityRule}}</div>
        <div class="post-wrapper" ref="contentScroll">
            <div class="goodsList">
                <div class="goodsItem" v-for="(goodsItem,goodsIndex) in goodsList" :key="'goodsItem'+goodsIndex"
                     @click="goToProduct(goodsItem)">
                    <div class="goodsImg">
                        <img :src="goodsItem.spuPicUrl" alt="">
                        <div class="outSellBox" v-show="goodsItem.storeNumber==0">补货中</div>
                        <div class="jinshengNum" v-show="goodsItem.storeNumber<=goodsItem.threshold&&goodsItem.storeNumber!=0"
                        >
                            仅剩{{goodsItem.storeNumber}}件
                        </div>
                    </div>
                    <div class="goodsInfo">
                        <div class="goodsName">{{goodsItem.spuName}}</div>
                        <div class="goodsDesc">{{goodsItem.spuSalePoint}}</div>
                        <div class="goodsSale">月售{{goodsItem.showSale  | threshold}}</div>
                        <div class="goodsPriceBx">
                            <div class="goodsPriceInfo">
                                <div class="goodsPrice">
                                    <div class="goodsPriceIcon">￥</div>
                                    <div class="goodsPriceNum">{{goodsItem.skuPrice|priceFilters}}</div>
                                </div>
                                <div class="goodsLinePrice" v-if="goodsItem.priceLine">
                                    ￥{{goodsItem.priceLine|priceFilters}}
                                </div>
                            </div>
                            <div class="addMinusBtn">
                                <div class="clickBox"  @click.stop="addGoodsNum(goodsItem,goodsIndex)"></div>
                                <div class="cartNumBox" v-show="goodsItem.cartCount!=0">
                                    <div class="cartNum">{{goodsItem.cartCount}}</div>
                                </div>
                                <img class="addBtn" ref="addBtn" src="@/assets/images/themeImg/icon_add.png" alt="">
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div class="fixedHeight" :style="{height:30+44+this.$footBarHeight+'px'}"></div>
        </div>
        <transition @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
            <img class="circle" v-show="isShowCircle" src="@/assets/images/mall/mall_icon_add.png" alt="">
        </transition>
    </div>
</template>

<script>
    import {
        getActSpuListService,
        getSkuTypeNumService,
        addGoodsService
    } from "../../services/goodsService";
    import { Toast, Dialog} from "vant";
    import NavigationBar from "@/common/currency/NavigationBar.vue";
    export default {
        name: "reducedPackaging",
        data(){
            return{
                skuTypeNum:null,
                typeVal:0, //0满减优惠专区 1打包促销专区
                activityId:"", //活动id
                activityRule:null, //活动全规则
                goodsList:null, //商品列表
                scrollContentTop:null,

                addBtnIcon:null,
                addGoodsLoading:false,
                isShowCircle:false,
            }
        },
        components: {
            NavigationBar
        },
        filters: {
            threshold(data) {
                return data >= 10000 ?  (data/10000>=10?'9万+':Math.floor(data / 10000) + '万+') : data
            },
            priceFilters(data){
                return data >= 10000 ? Math.floor(data/10000*100)/100+ '万' : data
            }
        },
        created(){

        },
        activated(){
            if (this.$route.params.activityId) {
                this.goodsList= [];
                this.activityRule = null;
                this.$refs.contentScroll.scrollTop = 0;
                this.typeVal = this.$route.params.typeVal;
                this.activityId = this.$route.params.activityId;
            }else{
                this.$refs.contentScroll.scrollTop = this.scrollContentTop;
            }
            this.getActSpuList();
            this.getSkuTypeNum();
        },
        methods:{
             /** 根据活动编码查活动商品
              * actCode   活动编码
              * ownerCode 业主编码
              * */
            getActSpuList(){
                 let paramData = {
                     actCode: this.activityId,
                     ownerCode:this.$userInfo.OWNER_CODE,
                 }
                 getActSpuListService(paramData).then(res => {
                     if (res.resultCode == "0") {
                        this.activityRule = res.data.actRule;
                        this.goodsList = res.data.goodsList;
                     }
                 }).catch(error => {
                 })
            },
            /**
             * 查询购物车数量
             * ownerCode  Number  业主CODE
             */
            getSkuTypeNum(isAddGoods=false){
                let paramData = {
                    ownerCode:this.$userInfo.OWNER_CODE
                }
                getSkuTypeNumService(paramData).then(res=>{
                    if (res.resultCode == 0) {
                        if (isAddGoods){
                            Toast({
                                message: '添加购物车成功',
                                duration:1000
                            });
                        }
                        this.skuTypeNum = res.data.skuTypeNum;
                    }
                }).catch(error=>{
                })
            },
            // 进入前状态
            beforeEnter(el) {
                console.log(this.addBtnIcon.getBoundingClientRect())
                el.style.left = this.addBtnIcon.getBoundingClientRect().left + 'px';
                el.style.top = this.addBtnIcon.getBoundingClientRect().top + 'px';
                el.style.transform = 'scale(1)'
            },
            // 进入中
            enter(el, done) {
                // el.offsetWidth 强制html渲染动画
                // el.offsetWidth 这句话如何不写就不会有动画效果直接渲染的
                el.offsetWidth;
                el.style.left = this.$refs.cartIcon.getBoundingClientRect().left + 'px'
                el.style.top = this.$refs.cartIcon.getBoundingClientRect().top + 'px'
                el.style.transform = 'scale(.5)'
                done()
            },
            afterEnter(el) {
                this.isShowCircle = !this.isShowCircle
            },
            //点击加号，增加商品数量
            addGoodsNum(goodsItem, goodsIndex) {
                console.log(goodsItem)
                if (this.addGoodsLoading || this.isShowCircle) {
                    return;
                }
                this.addBtnIcon = this.$refs.addBtn[goodsIndex];
                this.isShowCircle = true;
                this.addGoodsLoading = true;
                let paramData = {
                    ownerCode: this.$userInfo.OWNER_CODE,
                    spuCode: goodsItem.spuCode,
                    skuNo: goodsItem.skuNo,
                    shopCode: goodsItem.shopCode,
                    activitySpuId: goodsItem.activitySpuId,
                    skuCount: 1,
                    mark: 1,
                }
                addGoodsService(paramData).then(res => {
                    if (res.resultCode == "0") {
                        Toast({
                            message: '添加购物车成功',
                            duration: 1000
                        });
                        goodsItem.cartCount += 1;
                        this.getSkuTypeNum();
                    }else if (res.resultCode == '20100' && res.data.result == '0') {
                        this.$toast('您所添加商品超过最大购买量了哦');
                    }
                    this.addGoodsLoading = false;
                }).catch(error => {
                    this.addGoodsLoading = false;
                })
                // if (goodsItem.skuShopNum < goodsItem.skuList[0].storeNumber) {
                //     if (goodsItem.skuShopNum < goodsItem.skuCountLimit) {
                //         goodsItem.skuShopNum++;
                //         this.totalMoneyNum += goodsItem.skuList[0].skuPrice;
                //         if (goodsItem.priceLine == "") {
                //             this.lineMoneyNum += goodsItem.skuList[0].skuPrice;
                //         } else {
                //             this.lineMoneyNum += Number(goodsItem.priceLine);
                //         }
                //
                //     } else {
                //         Toast("超过限购数量");
                //     }
                // } else {
                //     Toast("库存不足");
                // }
            },
            //去商品详情页
            goToProduct(goodsItem) {
                this.$router.push({
                    name: 'goodsDetail',
                    params: {
                        spuCode: goodsItem.spuCode,
                        goodsUrl: goodsItem.spuPicUrl
                    }
                })
            },
            goToShopCart(){
                this.$router.push({
                    name: 'shoppingCart',
                    params: {}
                })
            },
            goBack() {
                this.$router.back(-1);
            }
        },
        beforeRouteLeave(to, form, next) {
            this.scrollContentTop = this.$refs.contentScroll.scrollTop;
            next();
        }
    }
</script>

<style scoped lang="scss">
    .circle{
        width: 22px;
        height: 22px;
        position: fixed;
        z-index: 100;
        transition: all .3s ease;
    }
    .flex-page{
        width: 100%;
        height: 100%;
        position: absolute;
        .post-wrapper{
            height: 100%;
            overflow-y: scroll;
            background: #f9f9f9;
        }
        .post-wrapper::-webkit-scrollbar {
            display: none;
        }
    }
    /deep/.navBar {
        height: 44PX!important;
        display: flex;
        align-items: center;
        .left,.right{
            height: 44PX!important;
        }
        .right{
            right: 5px;
            width: 44px!important;
            height: 44px!important;
            /*background: green;*/
            bottom: auto;
        }
    }
    .rightBar {
        width: 44px;
        height: 44px;
        position: relative;

        img {
            width: 32px;
            height: 32px;
            position: absolute;
            top: 50%; /*偏移*/
            left: 0;
            transform: translateY(-50%);
        }
        .goodsNum {
            position: absolute;
            font-size: 0.26667rem;
            color: #ffffff;
            text-align: center;
            -webkit-box-sizing: border-box;
            box-sizing: border-box;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            -webkit-box-pack: center;
            -ms-flex-pack: center;
            justify-content: center;
            -webkit-box-align: center;
            -ms-flex-align: center;
            align-items: center;
            background-color: #FC0238;
            z-index: 2;
            top: 0.13333rem;
            left: 0.42667rem;
            width: -webkit-max-content;
            width: -moz-max-content;
            width: max-content;
            min-width: 0.42667rem;
            height: 0.42667rem;
            line-height: 0.42667rem;
            border-radius: 0.21333rem;
        }
        .goodsNumMax{
            padding: 0 3px;
            min-width: 26px;
        }
    }
    .activityTitle{
        width: 100%;
        height: 30PX;
        text-align: center;
        line-height: 30PX;
        background: #F9F9F9;
        font-family: PingFangSC-Regular;
        font-size: 12px;
        color: #17B7BD;
        letter-spacing: 0;
    }
    .goodsList{
        background: #FFFFFF;
        width: 100%;
        overflow: hidden;
        .goodsItem{
            padding: 15px;
            display: flex;
            .goodsImg{
                width: 100px;
                height: 100px;
                position: relative;
                border-radius: 4px;
                overflow: hidden;
                img {
                    width: 100%;
                }
                .showActivitySpu{
                    position: absolute;
                    top: 0;
                    width: 40px;
                    height: 16px;
                    background: url("../../assets/images/mall/Spike_labelcut.png") no-repeat;
                    -webkit-background-size: cover;
                    background-size: cover;
                    text-align: center;
                    line-height: 16px;
                    font-family: PingFangSC-Regular;
                    font-size: 10px;
                    color: #FFFFFF;
                    letter-spacing: 0;
                }
                .jinshengNum{
                    width: 100%;
                    height: 25px;
                    text-align: center;
                    position: absolute;
                    bottom: 0;
                    background: rgba(0, 0, 0, 0.50);
                    font-size: 12px;
                    color: #BCBCBC;
                    letter-spacing: -0.04px;
                    line-height: 25px;
                }
                .outSellBox{
                    width: 70px;
                    height: 70px;
                    text-align: center;
                    line-height: 70px;
                    font-size: 12px;
                    color: #FFFFFF;
                    letter-spacing: -0.04px;
                    background: rgba(0,0,0,0.50);
                    border-radius: 50%;
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%);
                }
            }
            .goodsInfo{
                margin-left: 10px;
                .goodsName{
                    width: 235px;
                    font-family: PingFangSC-Medium;
                    font-size: 16px;
                    color: #333333;
                    letter-spacing: 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .goodsDesc{
                    width: 235px;
                    font-family: PingFangSC-Regular;
                    font-size: 14px;
                    color: #888888;
                    letter-spacing: 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .goodsSale{
                    width: 235px;
                    font-family: PingFangSC-Regular;
                    font-size: 14px;
                    color: #888888;
                    letter-spacing: 0;
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
                .goodsPriceBx {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    margin-top: 15px;
                    .goodsPriceInfo {
                        display: flex;
                        align-items: center;
                    }

                    .goodsPrice {
                        display: flex;
                        align-items: center;

                        .goodsPriceIcon {
                            font-family: PingFangSC-Medium;
                            font-size: 14px;
                            color: #F43D3D;
                            letter-spacing: -0.04px;
                        }

                        .goodsPriceNum {
                            font-family: PingFangSC-Medium;
                            font-size: 20px;
                            color: #F43D3D;
                            letter-spacing: -0.04px;
                            line-height: 14px;
                        }
                    }

                    .goodsLinePrice {
                        font-family: PingFangSC-Regular;
                        font-size: 14px;
                        color: #9A9A9A;
                        letter-spacing: -0.03px;
                        text-align: right;
                        line-height: 16px;
                        text-decoration: line-through;
                        margin-left: 2px;
                    }

                    .addMinusBtn {
                        display: flex;
                        position: relative;

                        .clickBox {
                            width: 55px;
                            height: 55px;
                            position: absolute;
                            top: -17px;
                            left: -17px;
                        }

                        .cartNumBox {
                            width: 18px;
                            height: 18px;
                            position: absolute;
                            top: -9px;
                            right: -9px;
                            background: #fff;
                            border-radius: 50%;

                            .cartNum {
                                width: 16px;
                                height: 16px;
                                margin: 1px auto 0px;
                                box-sizing: border-box;
                                border-radius: 50%;
                                border: 1px solid #17B7BD;
                                font-family: PingFangSC-Semibold;
                                font-size: 10px;
                                color: #17B7BD;
                                letter-spacing: -0.03px;
                                text-align: center;
                                line-height: 16px;
                            }

                        }

                        .cartNumBigBox {
                            width: 28px;
                            height: 18px;
                            border-radius: 30px;

                            .cartNum {
                                width: 26px;
                                height: 16px;
                                border-radius: 30px;
                                margin: 1px auto 0px;
                                box-sizing: border-box;
                                border: 1px solid #17B7BD;
                                font-family: PingFangSC-Semibold;
                                font-size: 10px;
                                color: #17B7BD;
                                letter-spacing: -0.03px;
                                text-align: center;
                                line-height: 15px;
                            }
                        }

                        .MinusBtn {
                            width: 22px;
                            height: 22px;
                        }

                        .goodsNum {
                            width: 30px;
                            font-size: 16px;
                            color: #333333;
                            letter-spacing: -0.06px;
                            text-align: center;
                            line-height: 22px;
                        }

                        .addBtn {
                            width: 22px;
                            height: 22px;
                        }
                    }
                }
            }
        }
    }

</style>
